.App {
  text-align: center;
  background-color: rgb(250, 246, 241);
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.footer {
  padding: 1%
}

.row-api {
  display: flex;
}

.column-api {
  flex: 50%;
  padding-left: 3%;
  padding-right: 3%;
  text-align: left;
  border-color: lightslategray;
  border-radius: 8%;
  border-style: solid;
  background-color: rgb(210, 236, 244);
  font-family: 'Dancing Script', cursive;
}

.column-api-input {
  flex: 50%;
  padding-left: 3%;
  padding-right: 3%;
  text-align: center;
}